@import url(https://fonts.googleapis.com/css?family=Josefin+Sans|Josefin+Slab:700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: \201C \201D \2018 \2019; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, html input[type=button], input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], input[disabled] {
  cursor: default; }

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, figure {
  margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: '';
  clear: both;
  height: 0; }

* {
  box-sizing: border-box; }

.visuallyHidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0);
  overflow: hidden; }

html {
  scroll-behavior: smooth; }

ul, li {
  list-style: none;
  padding: 0;
  margin: 0; }

ol {
  margin: 0; }

h1, h2, h3, p {
  padding: 0;
  margin: 0; }

h1, h2, h3 {
  font-family: "Josefin Slab", serif; }

p, a, input, button {
  font-family: "Josefin Sans", sans-serif; }

.wrapper {
  width: 80%;
  margin: 0 auto; }
  @media (max-width: 768px) {
    .wrapper {
      width: 90%; } }

img {
  width: 100%; }

a {
  color: #b20061;
  text-decoration: none; }

.navButton {
  background: none;
  border: none;
  font-size: 3rem;
  color: #b20061;
  outline: none; }
  .navButton:hover {
    color: #350482; }

.hero {
  background: url(/projectSix/static/media/heroPlaceholder.2092a7ba.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 57.5vh;
  display: flex;
  justify-content: center;
  align-items: center; }
  .hero h1 {
    margin: 0 0 25px 0; }

.heroContent .innerBox {
  padding: 50px;
  border: 1px rgba(255, 255, 255, 0.75) solid; }
  @media (max-width: 768px) {
    .heroContent .innerBox {
      padding: 50px 0px; } }

.recommendationContent .innerBox {
  padding: 10px;
  border: 1px rgba(255, 255, 255, 0.75) solid; }

.heroContent,
.recommendationContent {
  background: rgba(255, 255, 255, 0.65);
  text-align: center;
  width: 50%;
  padding: 10px; }
  @media (max-width: 768px) {
    .heroContent,
    .recommendationContent {
      width: 80%; } }
  .heroContent input, .heroContent button,
  .recommendationContent input,
  .recommendationContent button {
    height: 40px;
    padding: 5px 15px;
    border: 1px rgba(0, 0, 0, 0.5) solid; }
  .heroContent input,
  .recommendationContent input {
    width: 65%; }
    .heroContent input:focus,
    .recommendationContent input:focus {
      box-shadow: 0px 0px 2px 2px #350482; }
    @media (max-width: 900px) {
      .heroContent input,
      .recommendationContent input {
        width: 100%;
        max-width: 300px; } }
    @media (max-width: 400px) {
      .heroContent input,
      .recommendationContent input {
        max-width: 200px; } }
  .heroContent button,
  .recommendationContent button {
    text-transform: uppercase;
    width: calc(35% - 15px);
    margin-left: 15px;
    background-color: #b20061;
    color: #ffe1f9;
    -webkit-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out; }
    .heroContent button:hover, .heroContent button:focus,
    .recommendationContent button:hover,
    .recommendationContent button:focus {
      background-color: #350482; }
    @media (max-width: 900px) {
      .heroContent button,
      .recommendationContent button {
        width: 100%;
        max-width: 300px;
        margin: 5px 0px; } }
    @media (max-width: 400px) {
      .heroContent button,
      .recommendationContent button {
        max-width: 200px; } }

.recommendationContainer {
  display: flex;
  height: 35vh; }

.restaurantRecommendation {
  width: calc(100% / 3);
  display: flex;
  justify-content: center;
  align-items: center; }

.restaurantRecommendation.num1 {
  background: url(/projectSix/static/media/restaurantRecommendation1Placeholder.bab054c6.jpg); }

.restaurantRecommendation.num2 {
  background: url(/projectSix/static/media/restaurantRecommendation2Placeholder.83b38e56.jpg); }

.restaurantRecommendation.num3 {
  background: url(/projectSix/static/media/restaurantRecommendation3Placeholder.e51818c5.jpg); }

.restaurantRecommendation.num1,
.restaurantRecommendation.num2,
.restaurantRecommendation.num3 {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center; }

