  .detailsContent .wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between; }
  .detailsContent h2 {
    text-align: center;
    padding: 20px 0px; }

.restaurantDetails {
  background: #ffffff;
  width: 47.5%;
  border: 1px solid #000000;
  border-radius: 10px;
  position: relative; }
  @media (max-width: 768px) {
    .restaurantDetails {
      width: 100%;
      margin-top: 50px; } }
  .restaurantDetails img {
    width: 100%;
    height: 400px;
    object-fit: cover; }
  .restaurantDetails .restaurantContactInfo, .restaurantDetails .restaurantReviews {
    padding: 20px 40px; }
    @media (max-width: 900px) {
      .restaurantDetails .restaurantContactInfo, .restaurantDetails .restaurantReviews {
        padding: 20px 10px; } }
  .restaurantDetails .flexContainerRestaurantDetails {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    padding-bottom: 10px; }
  .restaurantDetails .info {
    text-align: right;
    color: #b20061; }
  .restaurantDetails .starRating img {
    width: auto;
    height: 1rem;
    object-fit: contain; }

.backBtnContainer .backButton {
  display: flex;
  align-items: center;
  position: absolute;
  top: 20px;
  left: 20px; }

.backBtnContainer .backText {
  display: none;
  font-size: 1.5rem;
  width: 100%;
  margin-left: 10px; }
  @media (max-width: 768px) {
    .backBtnContainer .backText {
      display: block; } }

.reviewCard {
  border: 1px solid #000000;
  border-radius: 10px;
  overflow: hidden;
  margin: 0;
  padding: 0;
  width: 100%; }
  .reviewCard img {
    display: inline-block;
    height: 25px;
    width: 25px; }
  .reviewCard a {
    -webkit-transition: 0.6s;
    transition: 0.6s;
    display: block;
    text-align: center; }
  .reviewCard:hover a,
  .reviewCard:focus-within a {
    color: #350482; }
  .reviewCard p {
    padding: 10px; }

.nameOfUser {
  text-align: center;
  background: #b20061;
  color: #ffffff; }

.bikeInfo {
  padding: 20px 40px; }
  @media (max-width: 900px) {
    .bikeInfo {
      padding: 20px 10px; } }

.bikeDetails {
  background: #ffffff;
  width: 47.5%;
  border: 1px solid #000000;
  border-radius: 10px; }
  .bikeDetails h3 {
    margin: 10px 0; }
  .bikeDetails p {
    color: #b20061;
    margin-bottom: 40px; }
  .bikeDetails ul, .bikeDetails li {
    list-style-type: decimal;
    font-family: "Josefin Sans", sans-serif;
    list-style-position: inside;
    color: #b20061; }
  @media (max-width: 768px) {
    .bikeDetails {
      margin-top: 50px;
      width: 100%; } }

.mapContainer {
  height: 400px; }

.carousel {
  width: 100%;
  overflow: hidden;
  text-align: center;
  margin-top: 10px;
  height: 210px; }
  .carousel ol {
    display: flex;
    justify-content: center;
    position: absolute;
    width: calc(100% - 80px); }
    @media (max-width: 900px) {
      .carousel ol {
        width: calc(100% - 20px); } }
  .carousel li {
    list-style: disc;
    width: 20px;
    cursor: pointer;
    color: #b20061; }
  .carousel li:hover, .carousel li:focus {
    color: #350482; }

footer {
  height: 7.5vh;
  text-align: center;
  background: #b20061;
  color: #ffffff;
  display: flex;
  align-items: center; }
  footer a {
    color: #ffffff;
    -webkit-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out; }
    footer a:hover {
      color: #350482; }
  footer .scrollToTop {
    position: fixed;
    bottom: 60px;
    right: 20px; }

